.card {
  border-radius: 10px;
  border: solid gray;
  text-align: center;
  width: 20%;
  margin-top: 10rem;
}
.imgProject {
  width: 100%;
  border-radius: 5px;
  border-bottom: none;
}
.project {
  display: flex;
  justify-content: space-around;
}
.btnCard {
  border-radius: 5px;
  color: white;
  background-color: black;
  width: 3rem;
  margin-top: 1rem;
  font-size: 1rem;
  margin-bottom: 1rem;
}
.btnCard:hover {
  cursor: pointer;
}
