.presentation {
  display: flex;
  justify-content: space-around;
}
.imgFlower {
  margin-top: 2rem;
  margin-left: 3rem;
  width: 40%;
  border-radius: 5px;
}
.name {
  padding-top: 5rem;
  text-align: center;
  font-family: "Open Sans";
  font-weight: 200;
  letter-spacing: 2rem;
  font-size: 2.5rem;
  color: rgb(30, 30, 30);
}
.presentationText {
  color: gray;
  text-align: center;
  font-family: "Open Sans";
  font-weight: 200;
  letter-spacing: 0.2rem;
  font-size: 1rem;
  margin: 3rem;
}
.profession {
  text-align: left;
  font-family: "Open Sans";
  font-weight: 200;
  letter-spacing: 1rem;
  font-size: 1rem;
  color: darkgray;
  margin-top: 2rem;
  margin-left: 3rem;
  text-align: center;
}
